var React = require('react');

var {IntlMixin, FormattedMessage} = require('react-intl');

var Router = require('react-router');
var {DefaultRoute, NotFoundRoute, Route, Link, Redirect, RouteHandler} = require('react-router');

var {Navbar, Nav} = require('react-bootstrap');

var {NavItemLink} = require('react-router-bootstrap');

var NotFound = React.createClass({
  render: function () {
    return (
      <p>Page not found.</p>
    );
  }
});

var Home = require('./Home');
var Contacts = require('./Contacts');
var About = require('./About');

require('../styles/flaticon.css');

var App = React.createClass({
  mixins: [IntlMixin],

  render: function () {
    const iotokLogo =
      <a className="logo" href="/">
        <span className="iotok-glyph flaticon-smartphone19"></span>
        <span className="iotok-glyph flaticon-gps27"></span>
        <span className="iotok-glyph flaticon-square181"></span>
        <span className="iotok-glyph flaticon-wifi83"></span>
        <span className="iotok-glyph flaticon-videocall"></span>
      </a>;

    return (
      <div>
        {/*<header className="header">*/}
          <Navbar brand={iotokLogo}>
            <Nav right>
              <NavItemLink eventKey={1} to="home">
                <FormattedMessage message={this.getIntlMessage('menu.home')} />
              </NavItemLink>
              <NavItemLink eventKey={2} to="contacts">
                <FormattedMessage message={this.getIntlMessage('contacts')} />
              </NavItemLink>
              <NavItemLink eventKey={3} to="about">
                <FormattedMessage message={this.getIntlMessage('menu.about')} />
              </NavItemLink>
            </Nav>
          </Navbar>
        {/*</header>*/}

        {/* this is the important part */}
        <div>
          <RouteHandler />
        </div>

        <div className="footer">
          <div className="container">
            <p className="text-center">
              <FormattedMessage message={this.getIntlMessage('iotok.address')} />
            </p>
            <p className="text-center">
              <FormattedMessage message={this.getIntlMessage('iotok.copyright')} />
            </p>
          </div>
        </div>
      </div>
    );
  }
});

var routes = (
  <Route name="app" path="/" handler={App}>
    <Route name="home" path="/" handler={Home} />
    <Route name="contacts" path="contacts" handler={Contacts} />
    <Route name="about" path="about" handler={About} />
    <DefaultRoute handler={Home} />
    <NotFoundRoute handler={NotFound}/>
    <Redirect from="company" to="about" />
  </Route>
);

Router.run(routes, Router.HistoryLocation, function (Handler) {
  React.render(<Handler {...enData}/>, document.body);
});



